          <li class="menu-link" tabindex=0 :class="{'active' : currentpage === 'home'}" @click="goTo('home')" >Home</li>
          <template v-if="!userNotRegistered">
            <li class="menu-link" tabindex=0 @click="goTo('movies')" :class="{'active' : currentpage === 'movies'}" >Movies</li>
            <li class="menu-link" tabindex=0 @click="goTo('events') " :class="{'active' : currentpage === 'events'}" >Events</li>
