                <a tabindex=0 class="menu-link" href="#" @click="goTo('invest')" :class="{'active' : currentpage === 'invest'}">Investment</a>
                <a tabindex=0 class="menu-link" href="#" @click="goTo('entertainment')" :class="{'active' : currentpage === 'entertainment'}">REDTV</a>
                <a tabindex=0 class="menu-link" href="#" @click="goTo('tremendoc')" :class="{'active' : currentpage === 'tremendoc'}">Health</a>
