<script>
export default {
    name: 'MenuItems',
    data () {
      return {
        showMenu: false
      }
    },
    props: {
      smallScreen: {
        type: Boolean,
        default: false
      }, 
      userNotRegistered: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      currentpage () {
        return this.$route.meta.pageName
      }
    },
    methods: {
      goTo (route) {
        this.showMenu = false
        return this.$router.push(`/redeem/${route}`)
      },
      openMenu () {
        this.showMenu = true
      },
      closeMenu () {
        this.showMenu = false
      },
      checkCurrentPage(value){
        return value === 'invest' || value === 'entertainment' || value === 'visa'|| value === 'tremendoc' ? value : ''
      }
     }

}
</script>

